import { Component, OnInit, Inject } from '@angular/core';
import { UserState, category2str, UserCategory, FlatUserCategory, FlatUserState, str2category } from 'src/app/utils/enums';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { UserInterface } from 'src/app/utils/interfaces';
import { AuthService } from 'src/app/services/auth.service';
import { PROVINCES } from 'src/app/utils/constants';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-user-dialog',
  templateUrl: './user-dialog.component.html',
  styleUrls: ['./user-dialog.component.scss']
})
export class UserDialog implements OnInit {

  myControl = new FormControl();
  filteredProvinces: Observable<string[]>;
  user: UserInterface;
  title: string;
  action: string;
  showPassword: Boolean;
  states = FlatUserState;
  categories;
  provinces = PROVINCES;

  constructor(
    public auth: AuthService,
    public dialogRef: MatDialogRef<UserDialog>,
    @Inject(MAT_DIALOG_DATA) public data: UserInterface | null) {

      var user: UserInterface = this.auth.getUser();
      var idx: UserCategory = str2category(user.category);
      this.categories = FlatUserCategory.slice(idx - 1, FlatUserCategory.length - 1);

      console.log(this.categories);

      this.showPassword = false;

      if ( this.data != null ) {
        this.user = {
          age: this.data.age,
          category: this.data.category,
          configured: this.data.configured,
          email: this.data.email,
          name: this.data.name,
          password: '',
          phone: this.data.phone,
          province: this.data.province,
          state: this.data.state,
          username: this.data.username,
        }
        this.title = "Editar usuario";
        this.action = "Editar";
      } else {
        this.user = {
          age: -1,
          category: category2str(UserCategory.user),
          configured: false,
          email: '',
          name: '',
          password: '',
          phone: '',
          province: '',
          state: UserState.PENDING,
          username: ''
        };
        this.title = "Crear usuario";
        this.action = "Guardar";
      }
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
    this.filteredProvinces = this.myControl.valueChanges
      .pipe(map(value => this._filter(value)));
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.provinces.filter(province => province.toLowerCase().includes(filterValue));
  }

  toggleShowPassword() {
    this.showPassword = !this.showPassword;
  }

}
