import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/services/user.service';
import { UserInterface } from 'src/app/utils/interfaces';
import { MatDialog } from '@angular/material';
import { UserDialog } from '../user-dialog/user-dialog.component';

var COLUMNS: string[] = [
  'name',
  'username',
  'email',
  'age',
  'phone',
  'province',
  'configured',
  'state',
  'category',
]

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {

  users: UserInterface[];
  columns = COLUMNS;
  loading: Boolean;
  error: Boolean;

  constructor(
    private dialog: MatDialog,
    private userService: UserService) {
    this.getUsers();
  }

  ngOnInit() {
  }

  getUsers() {
    this.loading = true;
    this.error = false;
    this.userService.getUsers().subscribe((result: UserInterface[]) => {
      this.users = [].concat(result);
      //console.log('USERS: ', this.users);
      this.loading = false;
      this.error = false;
    }, (err) => {
      console.log('users.component ERROR: ', err);
      this.error = true;
      this.loading = false;
    });
  }

  createUser(user: UserInterface) {

    const dialogRef = this.dialog.open(UserDialog, {
      data: null
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('RESULT: ', result);
    });
  }

}