import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { validateRegistration } from 'src/app/utils/validations';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  error: Boolean;
  errorMsg: string;

  constructor(
    private router: Router,
    private auth: AuthService) { }

  ngOnInit() {
  }

  setErrorMsg(msg: string): void {
    this.error = true;
    this.errorMsg = msg;
  }

  register(form: NgForm, value) {

    this.error = false;

    if ( form.status != 'VALID' ) {
      this.setErrorMsg('Faltan campos');
      return;
    }

    let result = validateRegistration(value);

    if ( result != 'OK' ) {
      this.setErrorMsg(result);
      return;
    }

    this.auth.register(value.email, value.password1)
      .subscribe((result) => {
        this.router.navigate(['/']);
      }, (err) => {
        this.setErrorMsg(err.json().message);
      });

  }

}