import { Injectable } from '@angular/core';
import { API_HOST_PORT } from '../config/server';
import { map } from 'rxjs/operators';
import { authHeaders } from '../utils/auth-options';
import { UserInterface } from '../utils/interfaces';
import { state2str } from '../utils/enums';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private http: HttpClient) { }

  get headers() {
    return authHeaders();
  }

  getUsers() {
    return this.http.
      get(API_HOST_PORT + '/users', { headers: this.headers }).
      pipe(map((result: UserInterface[]) => {
        return result.map((e: UserInterface) => {
          var obj = Object.create(e);
          obj.state = state2str(e.state);
          return obj;
        });
      }));
  }

}
